import { Injectable } from '@angular/core';
import { Observable, observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { importType } from '@angular/compiler/src/output/output_ast';
import { environment } from '../../../../../../environments/environment';
import { NuocSanXuatModel } from '../../../../../shared/models/nuoc-san-xuat.model';

@Injectable({providedIn: 'root'})

export class AddNoiSanXuatService {
    constructor (private httpClient: HttpClient) {}

    add(noiSanXuat: NuocSanXuatModel): Observable<any> {
        let url = environment.api.url + 'NuocSanXuat/add';
        return this.httpClient.put(url, noiSanXuat);
    }
}