import { Component, OnInit, Inject } from '@angular/core';
import { NuocSanXuatModel } from '../../../../../shared/models/nuoc-san-xuat.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AddNoiSanXuatService } from '../../quan-ly-noi-san-xuat/add-noi-san-xuat/add-noi-san-xuat.service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-add-noi-san-xuat',
  templateUrl: './add-noi-san-xuat.component.html',
  styleUrls: ['./add-noi-san-xuat.component.scss'],
})
export class AddNoiSanXuatComponent implements OnInit {
  _nuocSanXuat = new NuocSanXuatModel();
  showProgressBar = false;
  constructor(
    private __service: AddNoiSanXuatService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    public dialogMainRef: MatDialogRef<AddNoiSanXuatComponent>,
    private _alertDialog: MatDialog
  ) {}

  _title = 'Thêm nước sản xuất';
  _titleMainButton = 'Thêm';

  add() {
    this.showProgressBar = true;
    this.__service.add(this._nuocSanXuat).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.__snackbar.open('Thêm thành công', null, {
            duration: 2000,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
        this.dialogMainRef.close({
          data: this._nuocSanXuat,
          result: true,
        });
      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu lại. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._nuocSanXuat,
          result: false,
        });
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  ngOnInit(): void {}
}
