import { AddNoiSanXuatComponent } from './add-noi-san-xuat/add-noi-san-xuat.component';
import { Component, OnInit, ViewChild } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { NuocSanXuatModel } from '../../../../shared/models/nuoc-san-xuat.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyNoiSanXuatService } from './quan-ly-noi-san-xuat.service';
import { AlertDialogComponent } from '../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { UpdateNoiSanXuatComponent } from './update-noi-san-xuat/update-noi-san-xuat.component';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../main-nav.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-quan-ly-noi-san-xuat',
  templateUrl: './quan-ly-noi-san-xuat.component.html',
  styleUrls: ['./quan-ly-noi-san-xuat.component.scss'],
})
export class QuanLyNoiSanXuatComponent implements OnInit {
  displayedColumns = ['MaNuoc', 'TenNuoc', 'TrangThai', 'actions'];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public dialog: MatDialog,
    private __service: QuanLyNoiSanXuatService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.QUAN_LY_NOI_SAN_XUAT]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }
  ngOnInit(): void {
    this.loadData();
  }
  showProgressBar = true;
  _length = 0;
  dataSource: MatTableDataSource<NuocSanXuatModel>;
  _nuocSanXuats: NuocSanXuatModel[];

  //thanh trạng thái load

  private getDataNuocSanXuat() {
    this.showProgressBar = true;
    this.__service.GetAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._nuocSanXuats = data;
        this.dataSource = new MatTableDataSource(this._nuocSanXuats);
        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: NuocSanXuatModel,
          filter: string
        ) =>
          data.MaNuoc.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.TenNuoc.toLocaleLowerCase().indexOf(filter) != -1;

      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  themNuocSanXuat() {
    const dialogRef = this.dialog.open(AddNoiSanXuatComponent, {
      disableClose: true,
      width: '40rem',
      data: {},
    });
  }

  lock(noiSanXuat: NuocSanXuatModel) {
    const confirmDialogRef = this.dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey:
          'Bạn có chắn muốn đóng nước sản xuất ' + noiSanXuat.TenNuoc + '?',
        isConfirm: true,
      },
    });

    confirmDialogRef.afterClosed().subscribe((result) => {
      if (result === true) {
        this.showProgressBar = true;

        this.__service.lock(noiSanXuat).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.loadData();
              this.__snackbar.open('Đã đóng nước sản xuất', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }

  unLock(noiSanXuat: NuocSanXuatModel) {
    const confirmDialogRef = this.dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey:
          'Bạn có chắn muốn mở nước sản xuất ' + noiSanXuat.TenNuoc + '?',
        isConfirm: true,
      },
    });

    confirmDialogRef.afterClosed().subscribe((result) => {
      if (result === true) {
        this.showProgressBar = true;

        this.__service.unLock(noiSanXuat).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.loadData();

              this.__snackbar.open('Đã mở nước sản xuất', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  edit(noiSanXuat: NuocSanXuatModel) {
    const dialogRef = this.dialog.open(UpdateNoiSanXuatComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        noiSanXuat: noiSanXuat,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        this.loadData();
      }
    });
  }

  private loadData() {
    this.showProgressBar = true;
    this.getDataNuocSanXuat();
  }
  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
