import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { NuocSanXuatModel } from '../../../../../shared/models/nuoc-san-xuat.model';

@Injectable({ providedIn: 'root' })
export class UpdateNoiSanXuatService {
    constructor( private httpClient: HttpClient ) {}

    update(nuocSanXuat: NuocSanXuatModel): Observable<any> {
        let url = environment.api.url + 'NuocSanXuat/update';
        return this.httpClient.put(url, nuocSanXuat);
    }
}