import { Component, OnInit, Inject } from '@angular/core';
import { NuocSanXuatModel } from '../../../../../shared/models/nuoc-san-xuat.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { UpdateNoiSanXuatService } from './update-noi-san-xuat.service';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-update-noi-san-xuat',
  templateUrl: './update-noi-san-xuat.component.html',
  styleUrls: ['./update-noi-san-xuat.component.scss'],
})
export class UpdateNoiSanXuatComponent implements OnInit {
  showProgressBar = false;
  _nuocSanXuat = new NuocSanXuatModel();

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private __service: UpdateNoiSanXuatService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    public dialogMainRef: MatDialogRef<UpdateNoiSanXuatComponent>,
    private _alertDialog: MatDialog
  ) {}

  _title = 'Cập nhật';
  _titleMainButton = 'Cập nhật';

  ngOnInit(): void {
    if (this.data) {
      if (this.data.noiSanXuat) {
        this._nuocSanXuat = this.data.noiSanXuat;
      }
    }
  }

  update() {
    if (this._nuocSanXuat.TenNuoc == '') {
      this.__snackbar.open('Vui lòng nhập tên nước sản xuất!', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;

      this.__service.update(this._nuocSanXuat).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Cập nhật thành công', null, {
              duration: 2000,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
          let res = this._nuocSanXuat;
          this.dialogMainRef.close({
            data: this._nuocSanXuat,
            result: true,
          });
        },

        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._nuocSanXuat,
          result: false,
        });
      }
    });
  }
}
