import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../environments/environment';
import { NuocSanXuatModel } from '../../../../shared/models/nuoc-san-xuat.model';

@Injectable({providedIn: 'root'})
export class QuanLyNoiSanXuatService {
    constructor(private httpClient: HttpClient) { }

    GetAll(): Observable<any> {
      let url = environment.api.url + 'NuocSanXuat/getAll';
      return this.httpClient.get(url);
    }

    lock(noiSanXuat: NuocSanXuatModel): Observable<any> {
      let url = environment.api.url + 'NuocSanXuat/hide';
      return this.httpClient.put(url, noiSanXuat);
    }
    unLock(noiSanXuat: NuocSanXuatModel): Observable<any> {
      let url = environment.api.url + 'NuocSanXuat/show';
      return this.httpClient.put(url, noiSanXuat);
    }
}